    table {
        border-collapse: collapse;
        border: 1px solid #ddd;
        font-size: 14px;
        text-align: center;
        margin: auto;
        max-width: 800px;
        text-align: left;
    }
    th {
        background-color: #3498db;
        color: #fff;
        padding: 10px;
    }
    th:first-child {
        border-top-left-radius: 5px;
    }
    th:last-child {
        border-top-right-radius: 5px;
    }
    td, th {
        border: 1px solid #ddd;
        padding: 8px;
    }
    tr:nth-child(even) {
        background-color: #f2f2f2;
    }
    tr:hover {
        background-color: #f5f5f5;
    }
    .btn-danger {
        background-color: #e74c3c;
        color: #fff;
        border: none;
        padding: 5px 10px;
        border-radius: 3px;
        cursor: pointer;
    }
    .btn-danger:hover {
        background-color: #c0392b;
    }
    .results{
        margin-top: 60px;
        max-width: 800px;
        margin: auto;
        text-align: center;
      } 

    .Dashboardprofilephoto{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 1rem;
    }